
<template>
  <v-form>
    <v-container fluid grid-list-xl>
      <v-layout row wrap>
        <v-flex d-flex xs12 sm6>
          <v-card flat>
            <v-card-title class="title">Infrastructure</v-card-title>
            <v-card-text>
              <v-layout row wrap>
                <v-flex xs6 class="py-0">
                  <v-select
                    v-model="clusterIaas"
                    :items="cloudProviderItems"
                    label="Provider"
                    dense
                  ></v-select>
                </v-flex>
                <v-flex xs6 class="py-0">
                  <v-select
                    v-model="clusterRegion"
                    :items="regionItems(clusterIaas)"
                    label="Region"
                    flat
                  ></v-select>
                </v-flex>
                <v-flex xs12 class="py-0">
                  <v-select
                    v-if="clusterIaas === 'aws' || clusterIaas === 'gcp'"
                    v-model="zones"
                    :items="zoneItems(clusterIaas, clusterRegion)"
                    label="Zones"
                    small-chips
                    chips
                    multiple
                    flat
                  ></v-select>
                </v-flex>
              </v-layout>
            </v-card-text>
            <v-card-text v-if="clusterIaas === 'aws'">
              <v-subheader class="px-0 black--text">Credentials</v-subheader>
              <v-text-field
                v-model="accessKeyID"
                :rules="[rules.required, rules.min(30)]"
                counter="128"
                hint="e.g. AKIAIOSFODNN7EXAMPLE"
                label="Access Key ID"
                browser-autocomplete="off"
              ></v-text-field>
              <v-text-field
                v-model="secretAccessKey"
                :append-icon="secretAccessKeyVisible ? 'visibility' : 'visibility_off'"
                :rules="[rules.required, rules.min(40)]"
                :type="secretAccessKeyVisible ? 'text' : 'password'"
                label="Secret Access Key"
                hint="e.g. wJalrXUtnFEMIK7MDENG/bPxRfiCYzEXAMPLEKEY"
                counter="40"
                @click:append="secretAccessKeyVisible = !secretAccessKeyVisible"
              ></v-text-field>
            </v-card-text>
            <v-card-text v-else-if="clusterIaas === 'azure'">
              <v-subheader class="px-0 black--text">Credentials</v-subheader>
              <v-text-field
                v-model="clientID"
                :rules="[rules.required]"
                counter
                label="Client ID"
                browser-autocomplete="off"
              ></v-text-field>
              <v-text-field
                v-model="clientSecret"
                :append-icon="clientSecretVisible ? 'visibility' : 'visibility_off'"
                :rules="[rules.required]"
                :type="clientSecretVisible ? 'text' : 'password'"
                label="Client Secret"
                counter
                @click:append="clientSecretVisible = !clientSecretVisible"
              ></v-text-field>
              <v-text-field
                v-model="tenantID"
                :rules="[rules.required]"
                counter
                label="Tenant ID"
                browser-autocomplete="off"
              ></v-text-field>
              <v-text-field
                v-model="subscriptionID"
                :rules="[rules.required]"
                counter
                label="Subscription ID"
                browser-autocomplete="off"
              ></v-text-field>
            </v-card-text>
            <v-card-text v-else-if="clusterIaas === 'gcp'">
              <v-subheader class="px-0 black--text">Credentials</v-subheader>
              <v-text-field
                v-model="serviceaccountFilename"
                readonly
                placeholder="No file choosen"
                label="Service Account Key"
                hint="Choose service account key file in JSON format"
                @click="pickServiceaccount"
                @click:prepend="pickServiceaccount"
                prepend-icon="attach_file"
              ></v-text-field>
              <input
                type="file"
                style="display: none"
                ref="serviceaccountInput"
                @change="onServiceaccount"
              >
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex d-flex xs12 sm6>
          <v-card flat>
            <v-card-title class="title">Cluster</v-card-title>
            <v-card-text>
              <v-layout row wrap>
                <v-flex xs12 class="py-0">
                  <v-text-field
                    v-model="kubeconfigFilename"
                    readonly
                    placeholder="No file choosen"
                    label="Kubeconfig"
                    @click="pickKubeconfig"
                    @click:prepend="pickKubeconfig"
                    prepend-icon="attach_file"
                  ></v-text-field>
                  <input
                    type="file"
                    style="display: none"
                    ref="kubeconfigInput"
                    @change="onKubeconfig"
                  >
                </v-flex>
                <v-flex xs12 class="py-0">
                  <v-text-field
                    v-model="clusterDomain"
                    :rules="[rules.required]"
                    hint="e.g. mycluster.example.org"
                    persistent-hint
                    label="Domain"
                    browser-autocomplete="off"
                  ></v-text-field>
                </v-flex>

                <v-flex xs12 class="pb-0">
                  <v-subheader class="px-0 black--text">Administrator</v-subheader>
                </v-flex>
                <v-flex xs6 class="py-0">
                  <v-text-field
                    v-model="username"
                    :rules="[rules.required, rules.min(3)]"
                    hint="e.g. john.doe@example.org"
                    label="Email"
                    browser-autocomplete="off"
                  ></v-text-field>
                </v-flex>
                <v-flex xs6 class="py-0">
                  <v-text-field
                    v-model="password"
                    :append-icon="passwordVisible ? 'visibility' : 'visibility_off'"
                    :rules="[rules.required]"
                    :type="secretAccessKeyVisible ? 'text' : 'password'"
                    label="Password"
                    browser-autocomplete="off"
                    @click:append="secretAccessKeyVisible = !secretAccessKeyVisible"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 class="pb-0">
                  <v-subheader class="px-0 black--text">Networks</v-subheader>
                </v-flex>
                <v-flex xs4 class="py-0">
                  <v-text-field
                    v-model="networkNodeCidr"
                    :rules="[rules.required]"
                    hint="e.g. 10.132.0.0/16"
                    persistent-hint
                    label="Nodes CIDR"
                    browser-autocomplete="off"
                  ></v-text-field>
                </v-flex>
                <v-flex xs4 class="pt-0">
                  <v-text-field
                    v-model="networkPodCidr"
                    :rules="[rules.required]"
                    hint="e.g. 10.44.0.0/15"
                    persistent-hint
                    label="Pods CIDR"
                    browser-autocomplete="off"
                  ></v-text-field>
                </v-flex>
                <v-flex xs4 class="pt-0">
                  <v-text-field
                    v-model="networkServiceCidr"
                    :rules="[rules.required]"
                    hint="e.g. 10.47.240.0/20"
                    persistent-hint
                    label="Services CIDR"
                    browser-autocomplete="off"
                  ></v-text-field>
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex d-flex grow xs12>
          <v-card flat>
            <v-card-title class="headline">Getting started</v-card-title>
            <v-card-text>
              <v-layout row wrap>
                <v-flex xs12>
                  <p>This is a POC trying to install the Gardener through a pod.</p>
                  <h3>Deploy</h3>
                  <p>
                    Enter your data in the form above, download the
                    <v-btn
                      small
                      depressed
                      color="green lighten-5 green--text"
                      @click="onDeployDownload"
                    >
                      <tt class="text-lowercase">gardener-deploy.yaml</tt>
                    </v-btn>
                    <span class="ml-2">file</span> and apply it.
                  </p>
                  <div style="max-width: 800px">
                    <pre v-highlightjs><code class="reset bash">kubectl apply -f gardener-deploy.yaml</code></pre>
                  </div>
                  <br>
                  <p>To watch the installation progress run the following command.</p>
                  <div style="max-width: 800px">
                    <pre v-highlightjs><code class="reset bash">kubectl logs -f garden-setup-installer</code></pre>
                  </div>
                  <br>
                  <p>
                    The kubeconfig for the created
                    Garden can be found in the
                    <tt>garden</tt> namespace of the hosting Kubernetes cluster in the secret
                    <tt>garden-kubeconfig-for-admin</tt>.
                  </p>
                  <div style="max-width: 800px">
                    <pre v-highlightjs><code
  class="reset bash"
>kubectl get -n garden secret garden-kubeconfig-for-admin -o jsonpath='{.data.kubeconfig}' | base64 -d</code></pre>
                  </div>
                  <br>
                  <p v-if="!!clusterDomain">
                    The Garden Dashboard can be reached via the url
                    <a
                      :href="dashboardUrl"
                    >{{dashboardUrl}}</a>.
                  </p>
                  <br>
                  <h3>Delete</h3>
                  <p>
                    To delete the Gardener from your cluster download the
                    <v-btn
                      small
                      depressed
                      color="red lighten-5 red--text"
                      @click="onUndeployDownload"
                    >
                      <tt class="text-lowercase">gardener-undeploy.yaml</tt>
                    </v-btn>
                    <span class="ml-2">file</span> and apply it.
                  </p>
                  <div style="max-width: 800px">
                    <pre v-highlightjs><code class="reset bash">kubectl apply -f gardener-undeploy.yaml</code></pre>
                  </div>
                  <br>
                  <p>To watch the uninstallation progress run the following command.</p>
                  <div style="max-width: 800px">
                    <pre v-highlightjs><code class="reset bash">kubectl logs -f garden-setup-uninstaller</code></pre>
                  </div>
                  <br>
                  <p>Delete all resources created during the installation process.</p>
                  <div style="max-width: 800px">
                    <pre v-highlightjs><code class="reset bash">kubectl delete -f gardener-deploy.yaml</code></pre>
                  </div>
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-form>
</template>

<script>
import jsyaml from 'js-yaml'
import download from 'downloadjs'
import { mapGetters } from 'vuex'
import { handleTextFieldDrop } from '@/utils'

export default {
  name: 'Home',
  data: () => {
    return {
      name: 'myGardener',
      accessKeyID: null,
      secretAccessKey: null,
      secretAccessKeyVisible: false,
      serviceaccount: null,
      clientID: null,
      clientSecret: null,
      clientSecretVisible: null,
      subscriptionID: null,
      tenantID: null,
      kubeconfig: null,
      clusterIaas: 'gcp',
      clusterRegion: null,
      clusterDomain: null,
      zones: [],
      networkNodeCidr: null,
      networkPodCidr: null,
      networkServiceCidr: null,
      username: null,
      password: null,
      passwordVisible: false,
      files: [],
      kubeconfigFilename: null,
      serviceaccountFilename: null,
      rules: {
        required: value => !!value || 'Required.',
        min: length => {
          return value =>
            (value && value.length >= length) || `Min ${length} characters`
        },
      },
    }
  },
  computed: {
    ...mapGetters([
      'cloudProviderItems',
      'regionItems',
      'zoneItems',
      'backupType',
      'dnsType',
    ]),
    dashboardUrl() {
      return `https://gardener.ingress.${this.clusterDomain}`
    },
    landscapeName() {
      return this.clusterDomain
        ? this.clusterDomain.replace(/\./g, '-')
        : 'my-garden-cloud'
    },
    landscape() {
      return {
        name: this.landscapeName,
        cluster: {
          kubeconfig: './kubeconfig',
          domain: this.clusterDomain,
          iaas: this.clusterIaas,
          region: this.clusterRegion,
        },
        networks: {
          pods: this.networkPodCidr,
          nodes: this.networkNodeCidr,
          services: this.networkServiceCidr,
        },
        iaas: {
          region: this.clusterRegion,
          zones: this.zones,
          credentials: this.credentials,
        },
        etcd: {
          backup: {
            type: this.backupType(this.clusterIaas),
            region: this.clusterRegion,
            credentials: this.credentials,
          },
        },
        dns: {
          type: this.dnsType(this.clusterIaas),
          credentials: this.credentials,
        },
        identity: {
          users: [
            {
              email: this.username,
              username: this.username,
              password: this.password,
            },
          ],
        },
      }
    },
    acre() {
      return jsyaml.dump({
        credentials: this.credentials,
        landscape: this.landscape,
      })
    },
    credentials() {
      switch (this.clusterIaas) {
        case 'aws':
          return {
            region: this.clusterRegion,
            accessKeyID: this.accessKeyID,
            secretAccessKey: this.secretAccessKey,
          }
        case 'azure':
          return {
            clientID: this.clientID,
            clientSecret: this.clientSecret,
            subscriptionID: this.subscriptionID,
            tenantID: this.tenantID,
          }
        case 'gcp':
          return {
            'serviceaccount.json': this.serviceaccount,
          }
        default:
          return null
      }
    },
    deployPod() {
      return jsyaml.dump(this.getPod('deploy', '-An'))
    },
    deletePod() {
      return jsyaml.dump(this.getPod('delete', '-A'))
    },
    pvc() {
      return jsyaml.dump({
        apiVersion: 'v1',
        kind: 'PersistentVolumeClaim',
        metadata: {
          name: 'garden-setup-state',
          namespace: 'default',
        },
        spec: {
          accessModes: ['ReadWriteOnce'],
          resources: {
            requests: {
              storage: '1Gi',
            },
          },
        },
      })
    },
    secret() {
      return jsyaml.dump({
        apiVersion: 'v1',
        kind: 'Secret',
        metadata: {
          name: 'garden-setup-config',
          namespace: 'default',
        },
        type: 'Opaque',
        data: {
          kubeconfig: btoa(this.kubeconfig),
          'acre.yaml': btoa(this.acre),
        },
      })
    },
    deployManifest() {
      return [
        '---\n',
        this.secret,
        '---\n',
        this.pvc,
        '---\n',
        this.deployPod,
      ].join('')
    },
    undeployManifest() {
      return ['---\n', this.deletePod].join('')
    },
  },
  methods: {
    onDeployDownload() {
      download(this.deployManifest, 'gardener-deploy.yaml', 'text/yaml')
    },
    onUndeployDownload() {
      download(this.undeployManifest, 'gardener-undeploy.yaml', 'text/yaml')
    },
    getPod(command, ...args) {
      return {
        apiVersion: 'v1',
        kind: 'Pod',
        metadata: {
          name: `garden-setup-${command === 'delete' ? 'un' : ''}installer`,
          namespace: 'default',
        },
        spec: {
          restartPolicy: 'Never',
          containers: [
            {
              name: 'garden-setup-installer',
              image: 'diaphteiros/garden-setup:stable',
              imagePullPolicy: 'Always',
              command: ['./run.sh', '--color', command, ...args],
              resources: {},
              volumeMounts: [
                {
                  name: 'garden-setup-config',
                  mountPath: '/etc/garden-setup/config',
                },
                {
                  name: 'garden-setup-state',
                  mountPath: '/landscape',
                },
              ],
            },
          ],
          volumes: [
            {
              name: 'garden-setup-config',
              secret: {
                secretName: 'garden-setup-config',
              },
            },
            {
              name: 'garden-setup-state',
              persistentVolumeClaim: {
                claimName: 'garden-setup-state',
              },
            },
          ],
        },
      }
    },
    pickServiceaccount() {
      this.$refs.serviceaccountInput.click()
    },
    onServiceaccount(e) {
      const file = e.target.files[0]
      this.serviceaccountFilename = file.name
      const reader = new FileReader()
      reader.onload = () => {
        this.serviceaccount = reader.result
      }
      reader.readAsText(file)
    },
    pickKubeconfig() {
      this.$refs.kubeconfigInput.click()
    },
    onKubeconfig(e) {
      const file = e.target.files[0]
      this.kubeconfigFilename = file.name
      const reader = new FileReader()
      reader.onload = () => {
        this.kubeconfig = reader.result
      }
      reader.readAsText(file)
    },
    clickKubeconfigUpload() {
      this.$refs.kubeconfigUploadButton.querySelector('input').click()
    },
  },
  watch: {
    clusterIaas(value) {
      if (value === 'gcp') {
        this.$nextTick(() => {
          const onDrop = value => {
            this.serviceaccount = value
          }
          handleTextFieldDrop(this.$refs.serviceaccount, /json/, onDrop)
        })
      }
    },
  },
}
</script>

