import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  drawer: false,
  cloudProviders: {
    aws: {
      dns: 'aws-route53',
      storage: 's3',
      regions: {
        'eu-west-1': ['a', 'b', 'c'].map(suffix => 'eu-west-1' + suffix),
        'eu-central-1': ['a', 'b', 'c'].map(suffix => 'eu-central-1' + suffix),
        'us-east-1': ['a', 'b', 'c'].map(suffix => 'us-east-1' + suffix),
      },
    },
    azure: {
      dns: 'azure-dns',
      storage: 'abs',
      regions: {
        westeurope: undefined,
        eastus: undefined,
      },
    },
    gcp: {
      dns: 'google-clouddns',
      storage: 'gcs',
      regions: {
        'europe-west1': ['b', 'c', 'd'].map(suffix => 'europe-west1-' + suffix),
        'us-east1': ['b', 'c', 'd'].map(suffix => 'us-east1-' + suffix),
        'us-central1': ['a', 'b', 'c'].map(suffix => 'us-central1-' + suffix),
      },
    },
  },
}
const getters = {
  cloudProviderItems(state) {
    return Object.keys(state.cloudProviders)
  },
  backupType(state) {
    return key => {
      const cloudProvider = state.cloudProviders[key]
      return cloudProvider ? cloudProvider.storage : null
    }
  },
  dnsType(state) {
    return key => {
      const cloudProvider = state.cloudProviders[key]
      return cloudProvider ? cloudProvider.dns : null
    }
  },
  regionItems(state) {
    return key => {
      const cloudProvider = state.cloudProviders[key]
      return cloudProvider ? Object.keys(cloudProvider.regions) : []
    }
  },
  zoneItems(state) {
    return (key, region) => {
      const cloudProvider = state.cloudProviders[key]
      return cloudProvider ? cloudProvider.regions[region] : []
    }
  },
}

const actions = {}

const mutations = {
  setDrawer: (state, payload) => (state.drawer = payload),
  toggleDrawer: state => (state.drawer = !state.drawer),
}

const store = new Vuex.Store({
  state,
  getters,
  actions,
  mutations,
})

export default store
